@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";

@import "variables";

// 產險商品 3 x 2
.cathay-carosel{
  &.#{$prefix}-layout-top3{
    
    padding-top: 0px;

    @media (min-width: map-get($grid-breakpoints, 'md')){
      padding-top: 0px;
      // padding-bottom: 0;
    }

    .#{$prefix}slider{
      display: flex;
      flex-wrap: wrap;
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        display:block;
      }
      >div,>a{
      position: relative;
        flex: 1 1 100%;
        padding:60px 0px;
        margin-left:3%;
        margin-right:3%;
        margin-top:1.5%;
        margin-bottom:1.5%;
        border:1px solid #00a94f;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;

        @media (min-width: map-get($grid-breakpoints, 'md')){
          margin-left:0.5%;
          margin-right:0.5%;
          flex: 1 1 32%;
          &:first-child{
            margin-left:1%;
          }

          &:last-child{
            margin-right:1%;
          }
        }
        @media (min-width: map-get($grid-breakpoints, 'md')){
          padding:0;
          margin:0;
          border:0;
        }
      }
    }

    .slick-slide{

      position: relative;
        @media (min-width: map-get($grid-breakpoints, 'md')){
          padding:60px 0px;
          margin-left:3%;
          margin-right:3%;
          margin-top:1.5%;
          margin-bottom:1.5%;
          border:1px solid #00a94f;
          border-top-left-radius: 5px;
          border-top-right-radius: 5px;
          border-bottom-left-radius: 5px;
          border-bottom-right-radius: 5px;
        }
    }
    .#{$prefix}title{
      font-size:24px;
      font-weight: 400;
      line-height: 1;
      color:#00a94f;
      letter-spacing: 2px;

      @media (min-width: map-get($grid-breakpoints, 'md')){
        font-size:20px;
        letter-spacing: 3px;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
      }
    }

    .#{$prefix}subtitle{
      font-size:16px;
      font-weight: 400;
      line-height: 1.5;
      color:#333333;
        padding:0 10px;

      @media (min-width: map-get($grid-breakpoints, 'md')){
        font-size:18px;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
      }
    }
    .#{$prefix}content{
      font-size:16px;
      font-weight: 400;
      line-height: 1;
      color:#333333;

      @media (min-width: map-get($grid-breakpoints, 'md')){
      }
    }


    .#{$prefix}ribbon{
      position: absolute;
      top:20px;
      right:-10px;
      padding:5px 10px;
      color:#fff;
      line-height:1;
      font-size:16px;
      font-weight: 400;
      background-color: #f9556f;
      &:before{
        content: "";
        display: block;
        position: absolute;
        top:25px;
        right:0;
        margin:auto;
        width: 10px;
        height: 10px;
        border-bottom: 9px solid transparent;
        border-left: 10px solid;
        color:#c1243d;

      }
    }

    .scroll-wrapper{
      margin:35px 15px 35px 25px;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        margin:35px 8px 35px 15px;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
        // margin:35px 15px;
      }
    }
    .#{$prefix}more-content{
      display: none;
      // &::-webkit-scrollbar{width:6px;}
      // &::-webkit-scrollbar-track{background:#eaeaea;border-radius:4px;}
      // &::-webkit-scrollbar-thumb{background:#00a94f;border-radius:4px;}
      

      .scroll-element.scroll-x {
        display: none !important;
        width: 0;
        height: 0;
        opacity:0;
        visibility: hidden;
    }
      .scroll-element.scroll-y {
          visibility: hidden;
          opacity:0;
          transition: opacity .15s;
      }
      &[status="off"]{
        .scroll-element.scroll-y {
            visibility: hidden;
            opacity:0;
        }
      }
      &[status="on"]{
        .scroll-element.scroll-y {
            visibility: visible;
            opacity:1;
        }
      }
      height: 200px;
      overflow-y:auto;
      font-size:15px;
      font-weight: 400;
      line-height: 1.5;
      color:#333333;
      text-align:left;
      // padding-right:15px;

      @media (min-width: map-get($grid-breakpoints, 'md')){
       // margin:35px 8px 35px 15px;
        font-size:18px;
        height: 250px;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
        // margin:35px 15px;
      height: 190px;
      }
      ul,li,p{
        font-size:14px;
        @media (min-width: map-get($grid-breakpoints, 'md')){
          font-size:16px;
        }

        @media (min-width: map-get($grid-breakpoints, 'lg')){
          // margin:35px 15px;
        }
      }
      ul{
        padding-left: 15px;
      }
      .cac-row+.cac-row{
        margin-top: 15px;
      }


    }
    .cac-title{
      font-weight: bold;
      padding-left:15px;
      padding-right:15px;
      position: relative;
      &:before{
        content:'';
        position: absolute;
        display:block;
        width: 6px;
        height: 6px;
        background-color:#00a94f;
        top:10px;
        // bottom:0;
        left:0;
        margin:auto;
        border-radius:50%;
      }
    }
    .#{$prefix}price{
      color:#f9556f;
      font-size:30px;
      font-family:Verdana;
      font-weight: 400;
      line-height: 1;
    }

    .#{$prefix}txt-wrap{
      display: block;
      padding:0px;
      position: relative;
      margin-top: 10px;
      text-align:center;
      width: 100%;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        padding:0px 5px;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
      }
    }
    .#{$prefix}title + .#{$prefix}subtitle{
      margin-top: 15px;
    }
    .#{$prefix}subtitle + .#{$prefix}content{
      margin-top: 25px;
    }
    .#{$prefix}more-content + .#{$prefix}content{
      margin-top: 20px;
    }
    .#{$prefix}content + .#{$prefix}link{
      margin-top: 20px;
    }
    .#{$prefix}link + .#{$prefix}more{
      margin-top: 20px;
    }
    .#{$prefix}link{
      display: inline-block;
      font-size:17px;
      color:#fff;
      background-color: #00a94f;
      padding:15px 40px;
      border-top-left-radius: 50px;
      border-top-right-radius: 50px;
      border-bottom-left-radius: 50px;
      border-bottom-right-radius: 50px;

          
      @media (min-width: map-get($grid-breakpoints, 'md')){
        padding-left:30px;
        padding-right:30px;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
        padding-left:40px;
        padding-right:40px;
      }
    }
    .scroll-x{
      display:none;
    }
    .#{$prefix}more{
      display:block;
      font-size:16px;
      font-weight: 400;
      line-height: 1;
      color:#333333;
      letter-spacing:0px;
      position: relative;
      cursor:pointer;
      &:hover{
        color:#00a94f;
        &:after{
          border-top: 2px solid #00a94f;
          border-right: 2px solid #00a94f;
        }
      }
      &:after{
        content: "";
        width: 10px;
        height: 10px;
        border-top: 2px solid #666;
        border-right: 2px solid #666;
        transform: rotate(135deg);
        margin-left: 10px;
        position: relative;
        top:-3px;
        display: inline-block;
      }

      &[status="on"]{
        &:after{
            transform: rotate(-45deg);
            top:2px;
        }
      }
    }
    .#{$prefix}img-wrap{
      background-size: cover;
      overflow: hidden;
      background-repeat: no-repeat;
      background-position: center top;
      margin: 0 auto;
      vertical-align: middle;

      img{
        visibility: hidden;
      }

      $ratio:.5;
      width: round($ratio * 113px);
      height: round($ratio * 131px);
      
      @media (min-width: map-get($grid-breakpoints, 'md')){
        $ratio:.5;
        width: round($ratio * 113px);
        height: round($ratio * 131px);
      }
      
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        $ratio:.7;
        width: round($ratio * 113px);
        height: round($ratio * 131px);
      }
    }
    .slick-slide {
        border-radius: 5px;
    }


    
   .slick-slide {
      margin: 0 12px;
      padding:35px 0px;
      box-shadow: 1px 0px 5px 1px rgba(0,0,0,0);
      -webkit-box-shadow: 1px 0px 5px 1px rgba(0,0,0,0);
      -moz-box-shadow: 1px 0px 5px 1px rgba(0,0,0,0);

      transition: box-shadow .15s;
      transition-timing-function: ease-in-out;

      &:hover{
       -webkit-box-shadow: 0px 0px 15px -2px rgba(0,0,0,0.34);
      -moz-box-shadow: 0px 0px 15px -2px rgba(0,0,0,0.34);
      box-shadow: 0px 0px 15px -2px rgba(0,0,0,0.34);
      }
    }
    /* the parent */
    .slick-list {
      margin: 0;
    }

    .slick-track{
      margin-top: 15px;
      margin-bottom: 15px;
    }
  }
}